$cell-size: 100px;
$cell-gap: 1px;
$cell-color: #eee;
$calendar-padding: 10px;
$border-color: #000;
$start-days:
  "mon" 0,
  "tue" ($cell-size + $cell-gap),
  "wed" 2 * ($cell-size + $cell-gap),
  "thu" 3 * ($cell-size + $cell-gap),
  "fri" 4 * ($cell-size + $cell-gap),
  "sat" 5 * ($cell-size + $cell-gap),
  "sun" 6 * ($cell-size + $cell-gap);
